  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select--placeholder.is-open .custom-select__label {
  opacity: 1;
}
.custom-select--placeholder.not-empty .custom-select__label {
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__label, .custom-select.slide-up.is-open .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__text, .custom-select.slide-up.is-open .custom-select__text {
  opacity: 1;
}
.custom-select .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-select .input-message.is-invalid {
  color: #ff0000;
}
.custom-select .input-message.is-valid {
  color: #15ab07;
}

.contacts {
  border-top: 1px solid #74819c;
  width: 50%;
  padding-top: 56px;
  padding-left: 100px;
  padding-bottom: 30px;
}
@media (max-width: 1199px) {
  .contacts {
    border-top: none;
    width: 536px;
    padding-top: 61px;
    padding-left: 17px;
  }
}

.contacts .container {
  align-items: flex-start;
}
@media (max-width: 1199px) {
  .contacts .container {
    align-items: center;
    padding: 0 0;
  }
}

.contacts__wrapper {
  display: grid;
  grid-template-columns: 170px 170px;
  grid-template-rows: 1fr 1fr;
  grid-row-gap: 18px;
  grid-column-gap: 120px;
  grid-auto-flow: column;
  padding-left: 4px;
  padding-top: 2px;
  position: relative;
}
@media (max-width: 1199px) {
  .contacts__wrapper {
    grid-template-columns: 222px 222px;
    grid-column-gap: 92px;
    padding-left: 17px;
  }
}

.contacts__wrapper::before {
  position: absolute;
  content: "";
  width: 1px;
  height: 103px;
  background-color: #74819c;
  left: 234px;
  top: 8px;
}
@media (max-width: 1199px) {
  .contacts__wrapper::before {
    height: 129px;
    left: 283px;
    top: 10px;
  }
}

.contacts h2 {
  margin: 0;
  padding: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.62px;
