  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 150%;
  color: #102654;
  margin-bottom: 3px;
}
@media (max-width: 1199px) {
  .contacts h3 {
    font-size: 20px;
    margin-bottom: 7px;
  }
}

.contacts p {
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 152.344%;
  color: #102654;
}
@media (max-width: 1199px) {
  .contacts p {
    font-size: 18px;
  }
}

